@use "base/variables" as v;
@use "base/mixins" as m;

.productos {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;

  @include m.tablet {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .producto {
    color: v.$gris;
    height: 25rem;
    width: 18rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 1rem 1rem 1rem;
    border: 0.1rem solid v.$verde;
    border-radius: 0.5rem;
    transform: scale(1);
    transition: transform 0.3s ease-in-out;

    &:hover {
      transform: scale(1.1);
    }

    h3 {
      margin: 0.5rem 0;
      text-transform: uppercase;
      font-size: 2rem;
    }

    p {
      text-align: left;
      font-size: 1.6rem;
      margin: 0.5rem 0;
    }

    .acciones {
      margin: 0 auto;
      display: flex;
      justify-content: center;
      gap: 1rem;
      text-align: center;

      .boton-rojo {
        @include m.boton(v.$rojo, v.$blanco);
        margin: 0;
        font-size: 1.4rem;
        padding: 1rem 2rem;
        border-radius: 0.5rem;

        font-family: "Font Awesome 5 Free";
        font-weight: 900;
      }

      .boton-verde {
        @include m.boton(v.$verde, v.$blanco);
        margin: 0;
        font-size: 1.4rem;
        padding: 1rem 2rem;
        border-radius: 0.5rem;
      }
    }
  }

  thead {
    background-color: v.$verde;
    text-align: center;
    th {
      color: v.$blanco;
      padding: 1rem;
    }
  }

  tbody {
    text-align: center;
    tr:nth-child(odd) {
      background-color: v.$blanco;
    }

    td {
      padding: 0.5rem;
    }

    .acciones {
      margin: 0;
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      .boton-rojo {
        @include m.boton(v.$rojo, v.$blanco);
        margin: 0;
        font-size: 1.4rem;
        padding: 1rem 2rem;
        border-radius: 0.5rem;
      }

      .boton-verde {
        @include m.boton(v.$verde, v.$blanco);
        margin: 0;
        font-size: 1.4rem;
        padding: 1rem 2rem;
        border-radius: 0.5rem;
      }
    }
  }
}
