@use "base/variables" as v;
@use "base/mixins" as m;

.agregar-usuario {
  margin-left: 1rem;
  font-size: 2rem;
  cursor: pointer;
  display: inline-block;
  transition: transform 0.3s ease-in-out;
  color: v.$verde;

  &:hover {
    transform: scale(1.1);
  }
}

.ventas {
  width: 90%;
  margin: 0 auto;
  @include m.tablet {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
  #productos {
    @include m.telefono {
      @include m.grid(2, 1rem);
    }

    @include m.tablet {
      @include m.grid(4, 0.2rem);
    }

    .producto {
      margin: 1rem;
      padding: 1rem;
      border: 0.1rem solid black;
    }
  }

  .tabla-productos {
    border-spacing: 0;
    text-align: center;
    width: 100%;

    &.buscador {
      margin-bottom: 4rem;
    }

    thead {
      background-color: v.$azul;

      td {
        text-transform: uppercase;
        margin: 2rem;
      }
    }

    tbody {
      #contenedor-productos {
        td{
          width: 25%;
        }
        #codigoProducto {
          input {
            border: none;
            outline: none;
            padding: 1rem;
            width: 100%;
          }
        }
        #cantidadProducto {
          input {
            border: none;
            outline: none;
            padding: 1rem;
            width: 100%;
          }
        }
        #acciones {
          display: flex;
          span {
            display: inline-block;
            width: 3rem;
            margin: 0 0.2rem;
            padding: 0 0.4rem;
            font-size: 4rem;
            cursor: pointer;
            color: #fff;
          }
          .mas {
            color: v.$verde;
          }

          .menos {
            color: v.$rojo;
          }
        }
      }
    }
  }

  .factura {
    margin: 0 2rem;
    background-color: #f3eae8;
    box-shadow: -8px 8px 16px #c5bebc, 8px -8px 16px #ffffff;

    #total-ventas {
      font-weight: v.$bold;
      padding: 1rem 0;
      text-align: center;
      font-size: 3rem;
      color: v.$blanco;
      background-color: v.$azul;
      width: 100%;
      display: block;
    }

    .tipo-pago {
      padding-left: 1rem;
      label {
        color: v.$gris;
      }
    }

    .campos {
      padding: 0 1rem;
      .campo {
        label {
          text-transform: capitalize;
        }
      }
    }

    .campo-div {
      width: 100%;
      padding-right: 1rem;
      text-align: right;

      .label-total {
        font-size: 3rem;
        font-weight: v.$bold;
      }

      .total {
        font-size: 2.5rem;
        color: red;
        font-weight: v.$bold;
      }
    }
  }
}
