@use "base/variables" as v;
@use "base/mixins" as m;

.dark-mode {
  background-color: rgba(0, 0, 0, 0.76);
  transition: all .3s ease;

  .dashboard {
    .contenido {
      .nombre-pagina {
        color: v.$blanco;
        transition: all .3s ease;
      }

      .contenedor {
        .cuadros {
          .cuadro-contenido {
            background-color: v.$azulDark;
            transition: all .3s ease;
          }
        }
        .cuadros-botones {
          .cuadro-boton {
            background-color: v.$azulDark;
            transition: all .3s ease;

            &:hover {
              background-color: darken(v.$azulDark, 9);
              cursor: pointer;
            }
          }
        }
      }
    }

    .usuarios {
      .usuario {
        background-color: v.$azulDark;
        color: v.$blanco;
        transition: all .3s ease;
      }
    }

    .productos {
      .producto {
        background-color: v.$azulDark;
        color: v.$blanco;
        transition: all .3s ease;
      }
    }

    .formulario fieldset {
      background-color: transparent;
      border-color: v.$blanco;
      box-shadow: none;
      transition: all .3s ease;
      
      .legend {
        color: v.$blanco;

      }
    }

    .ventas .factura{
      background-color: transparent;
      border: .1rem solid v.$blanco;
      box-shadow: none;
      transition: all .3s ease;
    }
  }

  .campos{
    .campo{
      input{
        color: v.$blanco;
      }
    }   
  }
}
