@use "base/mixins" as m;
@use "base/variables" as v;

.formulario {
  fieldset {
    border: none;
    border: 0.2rem solid v.$verde;
    border-radius: 0.5rem;
    background-color: #f3eae8;
    box-shadow: 14px 14px 28px #e4dcda, -14px -14px 28px #fff8f6;

    label {
      color: v.$gris;
    }

    legend {
      padding: 0 1rem;
      margin-bottom: 2rem;
      color: v.$gris;

      &::selection {
        background-color: v.$rojo;
        color: v.$blanco;
      }
    }
  }

  select {
    color: v.$gris;
    margin: 1rem 0;
    padding: 0.5rem 1rem;
    border: none;
    border: 0.1rem solid v.$verde;
    border-radius: 0.5rem;
    outline: none;
  }

  .boton {
    @include m.boton(v.$verde, v.$blanco);
    padding: 1rem 2rem;
    margin: 2rem 0 1rem 0;
    font-size: 1.6rem;
  }
  .campos {
    @include m.tablet {
      @include m.grid(2, 2rem);
    }

    .campo {
      label {
        text-transform: uppercase;
        display: block;
        color: v.$gris;

        &::selection {
          background-color: v.$rojo;
          color: v.$blanco;
        }
      }

      input[type="text"],
      input[type="email"],
      input[type="tel"],
      input[type="password"],
      input[type="number"] {
        width: 100%;  
        padding: 1rem;
        border: none;
        outline: none;
        border-bottom: 0.2rem solid v.$verde;
        margin: 1rem 0 2rem 0;
        background-color: transparent;
        transition: all 0.5s ease;

        &:focus {
          border-bottom: 0.2rem solid v.$azul;
        }
      }

      input[type="search"]{
        width: 100%;
        margin: 1rem 0 2rem 0;
        padding: .5rem;
        border: none;
        outline: none;
        border-radius: .5rem;

        &:focus {
          border: .1rem solid v.$azul;
          border-radius: .5rem;
        }
  
      }

      input[type="file"] {
        margin: 1rem 0;
      }
    }
  }
}
