@use "base/variables" as v;
@use "base/mixins" as m;

.login {
  margin-bottom: 2rem;

  .formulario {
    &.login {
      .campos {
        @include m.tablet {
          @include m.grid(1, 0);
        }
      }
    }
  }
  .epsystem {
    background: linear-gradient(
      to right,
      v.$verde 10%,
      lighten(v.$verde, 20%) 100%
    );
    color: v.$verde;
    background-clip: text;
    font-size: 4rem;
    text-shadow: -2px 2px 2px rgba(0,0,0,0.73);

    &::selection {
      background-color: v.$verde;
      color: v.$blanco;
    }
  }

  .logo-imagen {
    transition: transform 0.3s ease-in-out;
    @include m.tablet {
      width: 40%;
      margin: 0 auto;
    }

    &.mostrar {
      transform: translateX(0);
    }
  }
  .contenedor-sm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    

    .descripcion {
      color: v.$gris;
    }
    .boton {
      @include m.boton(v.$verde, v.$blanco);
      margin: 4rem auto 0 auto;
    }
  }

  .acciones {
    a {
      color: v.$verde;
      font-weight: v.$bold;
      &::selection {
        background-color: v.$verde;
        color: v.$blanco;
      }
    }
  }
}