@use "base/variables" as v;
@use "base/mixins" as m;

.proveedores {
  width: 100%;
  margin-bottom: 2rem;
  @include m.telefono {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .proveedor {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 2rem;
    padding: 1rem;
    border-radius: 0.5rem;
    border: 0.1rem solid #0da6f3a5;
    color: v.$gris;
    width: 100%;
    overflow: hidden;

    @include m.telefono {
      width: 24rem;
      margin: 0;
    }
    h3 {
      margin: 0;
      margin-bottom: 1rem;
      font-size: 2rem;
    }

    p {
      margin: 0;
      margin-bottom: 1rem;
      font-size: 1.6rem;
    }
  }
}
