@use "base/variables" as v;
@use "base/mixins" as m;

.boton-verde-inline {
  display: block;
  border: none;
  color: v.$blanco;
  background-color: v.$verde;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  text-align: center;
  font-size: 1.5rem;

  @include m.telefono {
  }

  &:hover {
    background-color: darken(v.$verde, 10%);
  }
}

.boton-rojo-inline {
  width: 100%;
  height: 100%;
  border: none;
  color: v.$blanco;
  background-color: v.$rojo;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  text-align: center;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 1.5rem;

  &:hover {
    background-color: darken(v.$rojo, 10%);
    cursor: pointer;
  }
}
