@use "base/variables" as v;
@use "base/mixins" as m;

.dashboard {
  width: 100%;
  transition: all .3s ease-in-out;

  .principal {
    flex: 1;
  }

  .contenido {
    margin: 2rem;
    .nombre-pagina {
      font-size: 2.8rem;
      text-align: center;
      color: v.$gris;
    }

    .contenedor {
      .cuadros {
        text-align: center;

        @include m.telefono {
          @include m.grid(2, 1rem);
        }

        @include m.tablet {
          @include m.grid(4, 1rem);
        }

        .cuadro-contenido {
          background-color: v.$verde;
          margin-bottom: 1rem;
          border-radius: 0.5rem;
          padding: 1rem;
          position: relative;
          transition: all .3s ease;

          .info {
            color: v.$blanco;

            h2 {
              margin: 0 0 1rem 0;
            }

            p {
              margin: 0 0 1rem 0;
            }
          }

          .icono {
            font-size: 4rem;
            color: rgba(255, 255, 255, 0.642);
            position: absolute;
            height: 100%;
            top: 50%;
            left: 10%;
            bottom: 0;
            margin: auto;
          }
        }
      }

      .cuadros-botones {
        margin-top: 4rem;
        @include m.telefono {
          @include m.grid(2, 1rem);
        }

        @include m.tablet {
          @include m.grid(4, 1rem);
        }

        .cuadro-boton {
          display: block;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          margin: 2rem;
          border: 0.1rem solid v.$azul;
          border-radius: 1rem;
          transition: background-color 0.3s ease-in-out;
          transition: all .3s ease;

          @include m.telefono {
            margin: 0;
          }

          &:hover {
            background-color: darken(v.$azul, 0.6);
            color: v.$blanco;
            cursor: pointer;
          }

          .img {
            display: block;
            padding: 1rem 3rem 0 3rem;
            width: 100%;
          }

          .cuadro-texto {
            width: 100%;
            text-align: center;
            h3 {
              font-size: 2rem;
              padding: 0 2rem;
              height: 4rem;
            }
          }
        }
      }
    }
  }
}
