@use "base/variables" as v;
@use "base/mixins" as m;

.opciones {
  margin-bottom: 3rem;
  text-align: center;

  @include m.telefono {
    text-align: left;
  }

  .boton-crear {
    @include m.boton(v.$verde, v.$blanco);
    display: inline;
    padding: 1rem 2rem;
    font-size: 1.4rem;
  }

  .boton-volver{
    @include m.boton(v.$azul, v.$blanco);
    display: inline;
    padding: 1rem 2rem;
    font-size: 1.4rem;
  }
}



.usuarios {
  width: 100%;
  margin-bottom: 2rem;
  @include m.telefono {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .usuario {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 2rem;
    padding: 1rem;
    border-radius: 0.5rem;
    border: 0.1rem solid #0da6f3a5;
    color: v.$gris;
    width: 100%;
    overflow: hidden;

    @include m.telefono {
      width: 24rem;
      margin: 0;
    }
    h3 {
      margin: 0;
      margin-bottom: 1rem;
      font-size: 2rem;
    }

    p {
      margin: 0;
      margin-bottom: 1rem;
      font-size: 1.6rem;
    }

    .acciones {
      margin: 0;

      @include m.telefono {
        @include m.grid(2, 2rem);
      }

      form {
        margin-bottom: 0.5rem;

        @include m.telefono {
          margin: 0;
        }
      }
    }
  }
}


