@use "variables" as v;

::-webkit-scrollbar {
  width: 1.5rem;
}

::-webkit-scrollbar-thumb {
  background-color: v.$verde;
  border: 1rem;
  transition: all 0.3s ease-in;

  &:hover {
    background-color: darken(v.$verde, 8%);
  }
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}
body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f3eae8;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: v.$fuente_principal;
  font-size: 1.6rem;
}
.contenedor {
  width: 90%;
  max-width: 100rem;
  margin: 0 auto;
}
.contenedor-sm {
  max-width: 60rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}