@use 'base/variables' as v;

.alerta{
    text-transform: uppercase;
    font-weight: v.$bold;
    letter-spacing: .2rem;
    padding: calc(v.$separacion / 4);
    color: v.$blanco;
    width: 100%;
    display: block;
    margin-bottom: .5rem;

    &.error{
        background-color: v.$rojo;
    }

    &.exito{
        background-color: v.$verde;
    }
}