html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f3eae8;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 90%;
  max-width: 100rem;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 60rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
}

.boton-verde-inline {
  display: block;
  border: none;
  color: #F6f6f6;
  background-color: #329f00;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  text-align: center;
  font-size: 1.5rem;
}
.boton-verde-inline:hover {
  background-color: #226c00;
}

.boton-rojo-inline {
  width: 100%;
  height: 100%;
  border: none;
  color: #F6f6f6;
  background-color: #cb0000;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  text-align: center;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 1.5rem;
}
.boton-rojo-inline:hover {
  background-color: #980000;
  cursor: pointer;
}

.formulario fieldset {
  border: none;
  border: 0.2rem solid #329f00;
  border-radius: 0.5rem;
  background-color: #f3eae8;
  box-shadow: 14px 14px 28px #e4dcda, -14px -14px 28px #fff8f6;
}
.formulario fieldset label {
  color: #8c8c8c;
}
.formulario fieldset legend {
  padding: 0 1rem;
  margin-bottom: 2rem;
  color: #8c8c8c;
}
.formulario fieldset legend::selection {
  background-color: #cb0000;
  color: #F6f6f6;
}
.formulario select {
  color: #8c8c8c;
  margin: 1rem 0;
  padding: 0.5rem 1rem;
  border: none;
  border: 0.1rem solid #329f00;
  border-radius: 0.5rem;
  outline: none;
}
.formulario .boton {
  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #F6f6f6;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 5rem 0;
  padding: 1rem 2rem;
  margin: 2rem 0 1rem 0;
  font-size: 1.6rem;
}
@media (min-width: 76.8rem) {
  .formulario .boton {
    width: auto;
  }
}
.formulario .boton:hover {
  background-color: #226c00;
  cursor: pointer;
}
@media (min-width: 76.8rem) {
  .formulario .campos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formulario .campos .campo label {
  text-transform: uppercase;
  display: block;
  color: #8c8c8c;
}
.formulario .campos .campo label::selection {
  background-color: #cb0000;
  color: #F6f6f6;
}
.formulario .campos .campo input[type=text],
.formulario .campos .campo input[type=email],
.formulario .campos .campo input[type=tel],
.formulario .campos .campo input[type=password],
.formulario .campos .campo input[type=number] {
  width: 100%;
  padding: 1rem;
  border: none;
  outline: none;
  border-bottom: 0.2rem solid #329f00;
  margin: 1rem 0 2rem 0;
  background-color: transparent;
  transition: all 0.5s ease;
}
.formulario .campos .campo input[type=text]:focus,
.formulario .campos .campo input[type=email]:focus,
.formulario .campos .campo input[type=tel]:focus,
.formulario .campos .campo input[type=password]:focus,
.formulario .campos .campo input[type=number]:focus {
  border-bottom: 0.2rem solid #0da6f3;
}
.formulario .campos .campo input[type=search] {
  width: 100%;
  margin: 1rem 0 2rem 0;
  padding: 0.5rem;
  border: none;
  outline: none;
  border-radius: 0.5rem;
}
.formulario .campos .campo input[type=search]:focus {
  border: 0.1rem solid #0da6f3;
  border-radius: 0.5rem;
}
.formulario .campos .campo input[type=file] {
  margin: 1rem 0;
}

.acciones {
  margin-top: 4rem;
}

.alerta {
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.2rem;
  padding: 1.25rem;
  color: #F6f6f6;
  width: 100%;
  display: block;
  margin-bottom: 0.5rem;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

.login {
  margin-bottom: 2rem;
}
@media (min-width: 76.8rem) {
  .login .formulario.login .campos {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(1, 1fr);
  }
}
.login .epsystem {
  background: linear-gradient(to right, #329f00 10%, #54ff06 100%);
  color: #329f00;
  background-clip: text;
  font-size: 4rem;
  text-shadow: -2px 2px 2px rgba(0, 0, 0, 0.73);
}
.login .epsystem::selection {
  background-color: #329f00;
  color: #F6f6f6;
}
.login .logo-imagen {
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 76.8rem) {
  .login .logo-imagen {
    width: 40%;
    margin: 0 auto;
  }
}
.login .logo-imagen.mostrar {
  transform: translateX(0);
}
.login .contenedor-sm {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.login .contenedor-sm .descripcion {
  color: #8c8c8c;
}
.login .contenedor-sm .boton {
  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #F6f6f6;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 5rem 0;
  margin: 4rem auto 0 auto;
}
@media (min-width: 76.8rem) {
  .login .contenedor-sm .boton {
    width: auto;
  }
}
.login .contenedor-sm .boton:hover {
  background-color: #226c00;
  cursor: pointer;
}
.login .acciones a {
  color: #329f00;
  font-weight: 700;
}
.login .acciones a::selection {
  background-color: #329f00;
  color: #F6f6f6;
}

.dark-mode {
  background-color: rgba(0, 0, 0, 0.76);
  transition: all 0.3s ease;
}
.dark-mode .dashboard .contenido .nombre-pagina {
  color: #F6f6f6;
  transition: all 0.3s ease;
}
.dark-mode .dashboard .contenido .contenedor .cuadros .cuadro-contenido {
  background-color: #03a9f4;
  transition: all 0.3s ease;
}
.dark-mode .dashboard .contenido .contenedor .cuadros-botones .cuadro-boton {
  background-color: #03a9f4;
  transition: all 0.3s ease;
}
.dark-mode .dashboard .contenido .contenedor .cuadros-botones .cuadro-boton:hover {
  background-color: #028ac7;
  cursor: pointer;
}
.dark-mode .dashboard .usuarios .usuario {
  background-color: #03a9f4;
  color: #F6f6f6;
  transition: all 0.3s ease;
}
.dark-mode .dashboard .productos .producto {
  background-color: #03a9f4;
  color: #F6f6f6;
  transition: all 0.3s ease;
}
.dark-mode .dashboard .formulario fieldset {
  background-color: transparent;
  border-color: #F6f6f6;
  box-shadow: none;
  transition: all 0.3s ease;
}
.dark-mode .dashboard .formulario fieldset .legend {
  color: #F6f6f6;
}
.dark-mode .dashboard .ventas .factura {
  background-color: transparent;
  border: 0.1rem solid #F6f6f6;
  box-shadow: none;
  transition: all 0.3s ease;
}
.dark-mode .campos .campo input {
  color: #F6f6f6;
}

.dashboard {
  width: 100%;
  transition: all 0.3s ease-in-out;
}
.dashboard .principal {
  flex: 1;
}
.dashboard .contenido {
  margin: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
  color: #8c8c8c;
}
.dashboard .contenido .contenedor .cuadros {
  text-align: center;
}
@media (min-width: 35rem) {
  .dashboard .contenido .contenedor .cuadros {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 76.8rem) {
  .dashboard .contenido .contenedor .cuadros {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.dashboard .contenido .contenedor .cuadros .cuadro-contenido {
  background-color: #329f00;
  margin-bottom: 1rem;
  border-radius: 0.5rem;
  padding: 1rem;
  position: relative;
  transition: all 0.3s ease;
}
.dashboard .contenido .contenedor .cuadros .cuadro-contenido .info {
  color: #F6f6f6;
}
.dashboard .contenido .contenedor .cuadros .cuadro-contenido .info h2 {
  margin: 0 0 1rem 0;
}
.dashboard .contenido .contenedor .cuadros .cuadro-contenido .info p {
  margin: 0 0 1rem 0;
}
.dashboard .contenido .contenedor .cuadros .cuadro-contenido .icono {
  font-size: 4rem;
  color: rgba(255, 255, 255, 0.642);
  position: absolute;
  height: 100%;
  top: 50%;
  left: 10%;
  bottom: 0;
  margin: auto;
}
.dashboard .contenido .contenedor .cuadros-botones {
  margin-top: 4rem;
}
@media (min-width: 35rem) {
  .dashboard .contenido .contenedor .cuadros-botones {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 76.8rem) {
  .dashboard .contenido .contenedor .cuadros-botones {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.dashboard .contenido .contenedor .cuadros-botones .cuadro-boton {
  display: block;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 2rem;
  border: 0.1rem solid #0da6f3;
  border-radius: 1rem;
  transition: background-color 0.3s ease-in-out;
  transition: all 0.3s ease;
}
@media (min-width: 35rem) {
  .dashboard .contenido .contenedor .cuadros-botones .cuadro-boton {
    margin: 0;
  }
}
.dashboard .contenido .contenedor .cuadros-botones .cuadro-boton:hover {
  background-color: #0ca4f1;
  color: #F6f6f6;
  cursor: pointer;
}
.dashboard .contenido .contenedor .cuadros-botones .cuadro-boton .img {
  display: block;
  padding: 1rem 3rem 0 3rem;
  width: 100%;
}
.dashboard .contenido .contenedor .cuadros-botones .cuadro-boton .cuadro-texto {
  width: 100%;
  text-align: center;
}
.dashboard .contenido .contenedor .cuadros-botones .cuadro-boton .cuadro-texto h3 {
  font-size: 2rem;
  padding: 0 2rem;
  height: 4rem;
}

.header {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 1rem;
  background-color: rgba(13, 166, 243, 0.6470588235);
  position: relative;
}
@media (min-width: 35rem) {
  .header {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}
.header .barra {
  display: flex;
  justify-content: center;
  gap: 3rem;
  align-items: center;
  padding: 2rem 0 0 0;
}
@media (min-width: 35rem) {
  .header .barra {
    padding: 0;
  }
}
.header .barra .nombre {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #F6f6f6;
}
.header .barra .nombre .fas {
  font-size: 2rem;
}
.header .sidebar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 2rem 0;
}
.header .sidebar .logo {
  font-size: 3rem;
  color: #F6f6f6;
  font-weight: 900;
}
.header .sidebar .logo i {
  margin-right: 0.5rem;
}
.header .sidebar .logo span {
  color: #329f00;
}
.header .sidebar .sidebar-nav .links {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  text-align: center;
  background-color: rgba(13, 166, 243, 0.6470588235);
  z-index: 99999;
  transform: translateX(-200%);
  transition: transform 0.3s ease-in-out;
}
.header .sidebar .sidebar-nav .links.mostrar {
  transform: translateX(0);
}
.header .sidebar .sidebar-nav .links a {
  display: block;
  padding: 1rem 0;
  margin-bottom: 2rem;
  color: #F6f6f6;
  text-transform: uppercase;
  transition: background-color 0.3s ease-in-out;
}
.header .sidebar .sidebar-nav .links a i {
  margin: 0 2rem;
}
.header .sidebar .sidebar-nav .links a:hover {
  background-color: rgba(10, 133, 195, 0.6470588235);
}
.header .sidebar .sidebar-nav .menu-mobile {
  display: flex;
  gap: 2rem;
}
.header .sidebar .sidebar-nav .menu-mobile .fa-bars {
  font-size: 2rem;
  color: #F6f6f6;
  transform: scale(1);
  transition: transform 0.3s ease-in-out;
}
.header .sidebar .sidebar-nav .menu-mobile .fa-bars:hover {
  cursor: pointer;
  transform: scale(1.2);
}
.header .sidebar .sidebar-nav .menu-mobile .fa-circle-half-stroke {
  font-size: 2rem;
  color: #F6f6f6;
  transform: scale(1);
  transition: transform 0.3s ease-in-out;
}
.header .sidebar .sidebar-nav .menu-mobile .fa-circle-half-stroke:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.opciones {
  margin-bottom: 3rem;
  text-align: center;
}
@media (min-width: 35rem) {
  .opciones {
    text-align: left;
  }
}
