  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #F6f6f6;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 5rem 0;
  display: inline;
  padding: 1rem 2rem;
  font-size: 1.4rem;
}
@media (min-width: 76.8rem) {
  .opciones .boton-crear {
    width: auto;
  }
}
.opciones .boton-crear:hover {
  background-color: #226c00;
  cursor: pointer;
}
.opciones .boton-volver {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
