@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 1rem;
  background-color: #0da6f3a5;
  position: relative;

  @include m.telefono {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }

  .barra {
    display: flex;
    justify-content: center;
    gap: 3rem;
    align-items: center;
    padding: 2rem 0 0 0;

    @include m.telefono {
      padding: 0;
    }

    .nombre {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      color: v.$blanco;

      .fas {
        font-size: 2rem;
      }
    }
  }

  .sidebar {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    padding: 2rem 0;

    .logo {
      font-size: 3rem;
      color: v.$blanco;
      font-weight: v.$black;

      i {
        margin-right: 0.5rem;
      }

      span {
        color: v.$verde;
      }
    }

    .sidebar-nav {
      .links {
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        text-align: center;
        background-color: #0da6f3a5;
        z-index: 99999;
        transform: translateX(-200%);
        transition: transform 0.3s ease-in-out;

        &.mostrar {
          transform: translateX(0);
        }

        a {
          display: block;
          padding: 1rem 0;
          margin-bottom: 2rem;
          color: v.$blanco;
          text-transform: uppercase;
          transition: background-color 0.3s ease-in-out;

          i {
            margin: 0 2rem;
          }

          &:hover {
            background-color: darken(#0da6f3a5, 10%);
          }
        }
      }

      .menu-mobile {
        display: flex;
        gap: 2rem;
        
        .fa-bars {
          font-size: 2rem;
          color: v.$blanco;
          transform: scale(1);
          transition: transform 0.3s ease-in-out;
          
          &:hover {
            cursor: pointer;
            transform: scale(1.2);
          }
        }

        .fa-circle-half-stroke{
          font-size: 2rem;
          color: v.$blanco;
          transform: scale(1);
          transition: transform 0.3s ease-in-out;
          
          &:hover {
            cursor: pointer;
            transform: scale(1.2);
          }
        }
      }
    }
  }
}
